<template>
  <div class="upload-page">
    <div class="inputBox">
      <van-field v-model="name"  placeholder="您的姓名" class="nameBox" />
      <van-field v-model="missDate"  placeholder="和宇视相识日期" class="dateBox" readonly  @click="changDate"/>
    </div>
    <div class="chooseImg">
      <div class="selectImage">
        <img src="../assets/image/camera.png" alt="" @click="selectImage" v-if="!imgSrc" class="camera">
        <img :src="imgSrc" alt="" v-else @click="selectImage">
      </div>
      <div class="imgWord">选择您的回忆照片</div>
    </div>
    <div class="get-btn" @click="getCanvas">
      <van-icon name="arrow"/>
      下一步
    </div>
    <!--<div id="handlePage" >-->
      <!--<div class="handlePage-title">宇视十周年生日快乐!</div>-->
      <!--<div>-->
        <!--<van-field v-model="name" label="姓名" placeholder="请输入姓名" />-->
        <!--<van-field v-model="missDate" label="相识日期" placeholder="请选择相识日期" readonly  @click="changDate"/>-->
      <!--</div>-->
      <!--<div class="chooseImg">-->
        <!--<van-icon name="close" class="closeWrap" @click="clearImg" v-if="imgSrc"/>-->
        <!--<img src="../assets/image/camera.png" alt="" @click="selectImage" v-if="!imgSrc">-->
        <!--<img :src="imgSrc" alt="" v-else>-->
      <!--</div>-->
      <!--<div @click="getCanvas"  class="get-btn">点击生成</div>-->
    <!--</div>-->
  </div>

</template>

<script>
  export default {
    name: "getPoster",
    components:{
    },
    data() {
      return {
        name: '',
        imgSrc: '',
        imgUrl: '',
        missDate: '',
        pickerVisible: false,
        showCut:false
      }
    },
    mounted() {
    },
    methods: {
      clearImg(){
        this.imgSrc = ''
      },
      getCanvas() {
        if(!this.name){
          this.$toast('请填写名字')
          return
        }
        if(!this.missDate){
          this.$toast('请选择相识日期')
          return
        }
        if(!this.imgSrc){
          this.$toast('请选择图片')
          return
        }
        let day1 = new Date(this.missDate);
        let day2 = new Date();
        let tempObj = {
          imgUrl:'',
          name:this.name,
          imgSrc:this.imgSrc,
          days:Math.floor((day2 - day1) / (1000 * 60 * 60 * 24))
        }
        this.$emit('startPoster',tempObj)
      },

      changDate() {
        this.$emit('changeDate')
      },
      selectImage() {
        var fileInput = document.getElementById("fileInput");
        if (fileInput) {
          fileInput.parentNode.removeChild(fileInput);
          fileInput = null;
        }
        if (fileInput == null) {
          fileInput = document.createElement("input");
          fileInput.id = "fileInput";
          fileInput.type = "file";
          fileInput.accept = "image/*";
          fileInput.style.height = "0px";
          fileInput.style.display = "block";
          fileInput.style.overflow = "hidden";
          document.body.insertBefore(fileInput, document.body.firstChild);
          // if (navigator.userAgent.match(/Android/i)) {
          //     fileInput.addEventListener('change', tmpSelectFile, false);
          // } else {
          fileInput.addEventListener('change', this.selectFileImage, false);
          // }
        }
        setTimeout(function () {
          fileInput.click()
        }, 100);
      },
      selectFileImage(e){
        let _this = this
        var files = e.target.files[0]
        if (!e || !window.FileReader) return  // 判断是否支持FileReader
        let reader = new FileReader()
        reader.readAsDataURL(files) // 文件转换
        reader.onloadend = function () {
          _this.imgSrc = this.result
          _this.$emit('showCutDialog',_this.imgSrc)
          _this.textHide = false;
        }
      },
    }
  }
</script>

<style scoped>
  .inputBox{
    width: 4.8rem;
    height: 2.4rem;
    position: absolute;
    right: 0;
    left: 0;
    margin: auto;
    top:2.4rem;
    transform: rotate(90deg);
  }
  .nameBox{
    margin-bottom: 0.8rem;
  }
  .dateBox{
  }
  .chooseImg{
    width: 4rem;
    height: 4rem;
    background: rgba(13,151,221,0.8);
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    bottom: 2rem;
    border-radius: 0.2rem;
    padding-top: 0.2rem;
    transform: rotate(90deg);
  }
  .selectImage{
    width: 3.6rem;
    height: 3rem;
    background: #fff;
    margin: 0 auto;
    border-radius: 0.2rem;
    text-align: center;
    overflow: hidden;
  }
  .imgWord{
    width: 100%;
    text-align: center;
    font-size: 0.3rem;
    color: #fff;
    line-height: 0.8rem;
  }
  .get-btn{
    width: 150px;
    height: 40px;
    line-height: 40px;
    border-radius: 40px;
    color: #333;
    font-size: 0.3rem;
    border: 0.08rem solid #fff;
    text-align: center;
    background: #fff;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    transform: rotate(90deg);
    left: -0.8rem;
  }
  .selectImage img{
    width: 3.6rem;
    height: 3rem;
  }
  .selectImage img.camera{
    width: 2rem;
    height: 2rem;
    margin-top: 0.5rem;
  }
</style>
<style>
  .inputBox .van-cell{
    width: 4.8rem;
    height: 0.8rem;
    border-radius: 0.4rem;
    line-height: 0.4rem;
    text-align: center;
    background: rgba(65,141,202,0.8);
    border: none;
  }
  .inputBox .van-field__control{
    color: #fff!important;
    border: none;
  }
  .inputBox .van-field__control::-ms-input-placeholder {
    text-align: center;
  }
  .inputBox .van-field__control::-webkit-input-placeholder {
    text-align: center;
  }
  .van-cell::after{
    border-bottom: none!important;
  }
</style>
<style type="text/css">
  input.text{text-align:center}
</style>
