<template>
  <div>
    <audio ref="audio" :src="'https://res.qiaomukeji.com/h5pro/202110/ljbgm/bgm_21.mp3'" id="audio" preload="auto"
           autoplay="autoplay"></audio>
    <swiper class="swiper"
            ref="mySwiper"
            @slideChangeTransitionStart="slideChangeTransitionStart"
            :options="swiperOption"
            @slideChange="slideChange"
    >
      <!--class="swiper-no-swiping"-->
      <swiper-slide class="swiper-no-swiping">
        <div class="img-wrap loadingPage">

          <div class="calendarOut">
            <div class="percent">{{ percent+'%' }}</div>
            <div class="calendar">
              <div class="calendarItem">
                <div class="page-top">2011 1</div>
                <div class="date">99</div>
              </div>
              <!--animate__animated animate__rotateOutDownLeft-->
              <div :class="imgEnd3?'calendarItem animate__animated animate__rotateOutDownLeft ':'calendarItem'">
                <div class="page-top">2011 1</div>
                <div class="date">29</div>
              </div>
              <div :class="imgEnd2?'calendarItem animate__animated animate__rotateOutDownLeft ':'calendarItem'">
                <div class="page-top">2011 1</div>
                <div class="date">28</div>
              </div>
              <div :class="imgEnd1?'calendarItem animate__animated animate__rotateOutDownLeft ':'calendarItem'">
                <div class="page-top">2011 1</div>
                <div class="date">30</div>
              </div>
            </div>
          </div>

          <div class="nextTo" :class="percent == '0'?'animate__animated animate__fadeInUp':''">
            <div class="nextWord">
              <div class="">点击右侧</div>
              <div class="">观看宇视回忆故事</div>
              <div class="">生成专属海报</div>
            </div>
            <div class="iconAll" @click="clickToNnext">
              <van-icon name="arrow-down" class="arrow-down"/>
              <van-icon name="arrow-down" class="arrow-down"/>
            </div>
          </div>
        </div>
      </swiper-slide>
      <swiper-slide v-for="(item,index) in swiperImg" :key="index" class="swiper-no-swiping">
        <div :class="activeIndex == index+1?'img-wrap animate__animated animate__pulse':'img-wrap'">
          <img :src="item" alt="" class="back">
        </div>
      </swiper-slide>

      <swiper-slide>
        <!--<img src="../assets/image/twelve.jpg" alt="" class="back lastBack">-->
        <!--<div class="img-wrap" style="background: white">-->
        <!--</div>-->
        <div class="uploadPage">
          <getPoster @changeDate="changeDate" ref="getPoster" @startPoster="startPoster"
                     @showCutDialog="showCutDialog"></getPoster>
        </div>
      </swiper-slide>
      <div class="year-scroll" v-show="activeIndex != 11&&activeIndex !=0">
        <yearsScroll @changePage="changePage" ref="yearsScroll"></yearsScroll>
      </div>
      <!--<div class="swiper-pagination swiper-pagination-white" slot="pagination"></div>-->
      <div class="swiper-button-prev swiper-button-white" slot="button-prev"
           v-show="activeIndex != 11&&activeIndex !=0"></div>
      <div class="swiper-button-next swiper-button-white" slot="button-next"
           v-show="activeIndex != 11&&activeIndex !=0"></div>
    </swiper>
    <van-calendar v-model="pickerVisible" @confirm="onConfirm" :min-date="new Date('2000-10-10')" :color="'#408DCC'"/>
    <div class="postResult">
      <poster ref="posterMain" @exportImg="exportImg"></poster>
    </div>

    <div class="poster-wrap" v-if="showPoster">
      <postResult ref="postResult" v-if="showPoster"></postResult>
      <!--<van-overlay :show="showPoster">-->
        <!--<van-icon name="close" class="closeWrap" @click="showPoster = false"/>-->
        <!--<postResult ref="postResult" v-if="showPoster"></postResult>-->
        <!--&lt;!&ndash;<poster :tempObj="tempObj" ref="posterMain"></poster>&ndash;&gt;-->
      <!--</van-overlay>-->
    </div>
    <div v-if="showCut">
      <van-overlay :show="showCut">
        <cutImg :imgSrc="imgSrc" @closePage="closePage" @cancleCut="cancleCut"></cutImg>
      </van-overlay>
    </div>
    <!--每一项音频播放-->
    <audio ref="audioItem" :src="itemSrc" id="audioItem" v-if="itemSrc"></audio>

  </div>
</template>

<script>
  import postResult from './postResult'
  import cutImg from './cutImg'
  import yearsScroll from './yearsScroll'
  import poster from './poster'
  import {swiper, swiperSlide} from "vue-awesome-swiper";
  import "swiper/dist/css/swiper.css";
  import getPoster from './getPoster'

  export default {
    name: "page",
    components: {
      swiper,
      swiperSlide,
      getPoster,
      poster,
      yearsScroll,
      cutImg,
      postResult
    },
    data() {
      return {
        swiperImg: [
          // require('../assets/image/one.jpg'),
          require('../assets/image/five.jpg'),
          require('../assets/image/nine.jpg'),
          require('../assets/image/five.jpg'),
          require('../assets/image/nine.jpg'),
          require('../assets/image/five.jpg'),
          require('../assets/image/four.jpg'),
          require('../assets/image/nine.jpg'),
          require('../assets/image/three.jpg'),
          require('../assets/image/two.jpg'),
          require('../assets/image/nine.jpg'),
        ],
        ll: require('../assets/image/home_17.jpg'),
        itemSrc: '',
        imgSrc: '',
        showCut: false,
        showPoster: false,
        tempObj: '',
        activeIndex: '',
        pickerVisible: false,
        swiperOption: {
          direction: 'vertical',
          speed: 300,
          effect: 'fade',
          pagination: {
            el: '.swiper-pagination',
            clickable: true
          },
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev'
          }
        },
        percent: '',
        t: null,
        imgEnd1: false,
        imgEnd2: false,
        imgEnd3: false,
        effect: 'fade'

      }
    },
    mounted() {
      this.$nextTick(() => {
        this.audioAutoPlay('audio')
        let num = 10
        this.percent = num * 10
        this.t = setInterval(() => {
          num--
          this.percent = num * 10
          if (num == 9) {
            this.imgEnd1 = true
          }
          if (num == 5) {
            this.imgEnd2 = true
          }
          if (num == 2) {
            this.imgEnd3 = true
          }
          if (num == 0) {
            clearInterval(this.t)
            this.t = null
          }
        }, num * 40)
      })
      // this.togglePlay()
    },
    methods: {
      closePage(url) {
        if (url.indexOf('data:image/jpeg;base64,') > -1) {
          this.showCut = false
          this.imgSrc = url
          this.$refs.getPoster.imgSrc = url
        } else {
          this.$toast('上传不符合规定')
          this.cancleCut()
        }
      },
      cancleCut() {
        this.showCut = false
        this.imgSrc = ''
        this.$refs.getPoster.imgSrc = ''
      },
      showCutDialog(src) {
        this.showCut = true
        this.imgSrc = src
      },
      clickToNnext() {
        if (this.percent != '0') {
          return
        }
        // this.swiperOption.effect = 'fade'
        this.activeIndex = 1
        this.$refs.mySwiper.swiper.slideTo(1, 500, false);//切换到第一个slide，速度为1秒

      },
      slideChangeTransitionStart() {
        this.activeIndex = this.$refs.mySwiper.swiper.activeIndex
      },
      slideChange() {
        this.activeIndex = this.$refs.mySwiper.swiper.activeIndex
        this.$nextTick(() => {
          this.$refs.yearsScroll.action(this.activeIndex)
        })
        if (this.activeIndex == 1) {
          this.itemSrc = "https://res.qiaomukeji.com/h5pro/202110/lj1/m1.mp3?v=24"
          this.$nextTick(() => {
            let audio = document.getElementById('audioItem');
            audio.play()
          })
        } else if (this.activeIndex == 2) {
          this.itemSrc = "https://res.qiaomukeji.com/h5pro/202110/ljabm/p2.mp3?v=24"
          this.$nextTick(() => {
            let audio = document.getElementById('audioItem');
            audio.play()
          })
        } else if (this.activeIndex == 3) {
          this.itemSrc = "https://res.qiaomukeji.com/h5pro/202110/ljabm/p2.mp3?v=24"
          this.$nextTick(() => {
            let audio = document.getElementById('audioItem');
            audio.play()
          })
        } else {
          this.itemSrc = ''
        }
      },
      clickbtn() {
        this.$refs.audio.play()

        // let player = this.$refs.player
        // player.play()
      },
      audioAutoPlay(id) {
        var audio = document.getElementById(id);
        // audio.play();
        document.addEventListener("WeixinJSBridgeReady", function () {
          audio.play();
        }, false);
        document.addEventListener("YixinJSBridgeReady", function () {
          audio.play();
        }, false);
      },
      changeDate() {
        this.pickerVisible = true
      },
      formatDate(date) {
        return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`;
      },
      onConfirm(date) {
        this.pickerVisible = false;
        if(date.getTime() > new Date().getTime()){
          this.$toast('您选择的日期不符合规定')
          return
        }
        this.$refs.getPoster.missDate = this.formatDate(date)
      },
      startPoster(obj) {
        this.tempObj = obj
        console.log(obj)
        this.showPoster = true
        this.$nextTick(() => {
          this.$refs.posterMain.getResult(obj)
        })
      },
      exportImg(url) {
        this.$refs.postResult.imgObj.imgUrl = url
      },
      changePage(index) {
        this.activeIndex = index
        this.$refs.mySwiper.swiper.slideTo(index, 50, false);//切换到第一个slide，速度为1秒
      }
    }
  }
</script>

<style scoped>
  .swiper {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

  }

  .swiper-slide {
    height: 100%;
    width: 100%;
    position: relative;
  }

  .uploadPage {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    background: #0d97dd;

  }

  .img-wrap {
    width: 100%;
    height: 100%;
  }

  .loadingPage {
    background: #0d97dd;
    position: relative;
  }

  .nextTo {
    width: 100%;
    position: absolute;
    bottom: 1.3rem;
    text-align: center;
    font-size: 0.4rem;
    color: #fff;
    opacity: 0;
  }
  .nextTo .nextWord{
    font-size: 0.28rem;
    transform: rotate(90deg);
    line-height: 0.4rem;

  }

  .arrow-down {
    display: block;
    line-height: 0.24rem;
    font-size: 0.6rem;
  }

  .iconAll {
    margin-top: 0.8rem;
  }

  .percent {
    color: #fff;
    font-size: 0.48rem;
    width: 2rem;
    height: 0.3rem;
    text-align: center;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    margin: auto;
    bottom: -3.6rem;
  }
  .calendarOut{
    transform: rotate(90deg);width: 3.18rem;
    height: 3.58rem;
    position: absolute;
    left: 50%;
    margin-left: -1.79rem;
    top: 50%;
    margin-top: -2.39rem;
  }
  .calendar {
    background: url('../assets/image/calendar.png') no-repeat;
    background-size: 100% 100%;
    width: 3.18rem;
    height: 3.58rem;
    transform: scale(0.8);
  }
  .calendarItem {
    position: absolute;
    left: .6rem;
    top: .58rem;
    background: url('../assets/image/paper.png') no-repeat;
    background-size: 100% 100%;
    -webkit-transform-origin: top left;
    width: 2.38rem;
    height: 2.15rem;
    font-size: .4rem;
    text-align: center;
    line-height: 2.15rem;
  }

  .calendarItemEnd {
    opacity: 0;
    transition: all 1.2s;
  }

  .page-top {
    width: 1.78rem;
    height: .38rem;
    font-size: .32rem;
    line-height: .38rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #4a9cee;
    border-top: .01rem solid #4a9cee;
    border-bottom: .01rem solid #4a9cee;
    margin: .18rem auto 0 auto;
    padding: .02rem .18rem 0 .18rem;
    box-sizing: border-box;
    font-family: fzjhz_s;
  }

  .date {
    font-size: 1.56rem;
    line-height: 1;
    color: #4a9cee;
    text-align: center;
    font-family: fzjhc;
  }

  .back {
    overflow: hidden;
    transform: rotate(90deg) translate(calc(100vh / 2 - 100vw / 2), calc(100vh / 2 - 100vw / 2));
    width: 100vh;
    height: 100vw;
  }

  .lastBack {
  }

  .swiper-button-prev {
    top: 5%;
    left: 50%;
    margin: 0;
    margin-left: -10px;
    transform: rotate(90deg);

  }

  .swiper-button-next {
    top: unset;
    bottom: 5%;
    right: 50%;
    margin: 0;
    margin-right: -10px;
    transform: rotate(90deg);
  }

  .poster-wrap {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 99999;
  }

  .postResult {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: -1;
  }

  .closeWrap {
    position: absolute;
    right: 10px;
    top: 30px;
    z-index: 999;
    color: #fff;
    font-size: 0.6rem;
  }

  .swiper-container-vertical > .swiper-pagination-bullets {
    left: 10px;
  }

  .year-scroll {
    position: fixed;
    left: 10px;
    top: 30px;
    width: 60px;
    z-index: 999999;
  }

  .cutImg {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 99;
  }
  .uploadPage{
    background: url('../assets/image/home_21.jpg') no-repeat;
    background-size: 100% 100%;
  }

</style>
