      <div class="inner-content">
        <div class="inner-img">
          <img :src="imgObj.imgSrc" alt="">
        </div>
        <div class="inner-word">
          <div>"谢谢十年有你</div>
          <div>谢谢所有的共同度过和共同成长</div>
          <div class="nameBox">{{ imgObj.name }}：</div>
          <div>与宇视相识{{ imgObj.days }}天</div>
          <div>宇视十周年，生日快乐</div>
        </div>
      </div>
      <!--<div class="inner-content">-->
