<script>
  import html2canvas from 'html2canvas';
  export default {
    name: "poster",
    data(){
      return{
        imgObj:{
          imgSrc:'',
          days:'',
          name:''
        }
      }
    },
    methods:{
      getResult(obj){
        this.imgObj.imgSrc = obj.imgSrc
        this.imgObj.days = obj.days
        this.imgObj.name = obj.name
        let dom = document.getElementById('imgContainer')
        this.$nextTick(() =>{
          html2canvas(this.$refs.imgContainer, {
            // 转换为图片,
            taintTest: false,
            allowTaint: false,
            scale:window.devicePixelRatio,
            width:dom.offsetWidth,//设置canvas尺寸与所截图尺寸相同，防止白边
            height:dom.offsetHeight,//防止白边
            useCORS: true // 解决资源跨域问题
          }).then(canvas => {
            let dataURL = canvas.toDataURL('image/jpeg',1);
            this.$emit('exportImg',dataURL)
          });
        })
      }
    }
  }
</script>

